import React from 'react'
import { products } from '../../Data'
import  { useEffect, useState } from "react";
import "./Shaurma.scss"


const Shaurma = () => {

    const [array, setarray] = useState([]);
  
    useEffect(() => {
      setarray(products);
    }, []);
  
      const newArray = array.filter((el) => el.category === "shaurma");
    


  return (
    <div>
        <header>
            <div className="container">
            <h1 id='Shaurma' className='maks' >🌮Шаурма</h1>
                <div className="parentt">
                    {newArray.map((el) => {
                        return(
                                <div className="card">
                                    <img className='img' src={el.image} alt="" />
                                    <h1 className='h1'>{el.name}</h1>
                                    <p className='p'>{el.price} so'm</p>
                                    <button className='btn btn-warning'>Add to card</button>
                                </div>    
                              )
                    })}
                </div>
            </div>
        </header>
    </div>
  )
}

export default Shaurma