import React from 'react'
import { useCart } from "react-use-cart"
import "../KorzinkaText/KorzinkaText.scss"

import axios from 'axios';
//const axios = require('axios'); // legacy way


const KorzinkaText = () => {
    const {items, isEmpty, updateItemQuantity, removeItem} = useCart();

    let total = 0;


    const postTest = () => {
      axios.post(
        `https://api.telegram.org/bot5378253930:AAEW0rlP7j7KA50TxsypNSLLKvQ5jYnNPfc/sendMessage?
      chat_id=-1001553163227&text=${encodeURIComponent(
        `<b>Details:</b>
 
          ${items.map((item) => {
            return `
            <b>${item.name}</b>
            ${item.count} x ${item.price} $ = ${item.count}
            `;
          })
          .join("")}
          <b>Total:</b> ${total} $`
      )}&parse_mode=html`
      );
    }

  return (
    <div>
       {isEmpty ? <h1 className='ddd'>Cart is Empty</h1> :null} 

       {items.map((item) => {
        const prCount = item.count * item.price;
        total += prCount;

        if (item.count >= 1) {
          
          return (
            <div className="container">
              
              <div className="cardd">
              
                <div className="left">
                <img className='img' src={item.image} alt="" />
                <div className="text">
                <h1 className='h1'>{item.name}</h1>
                <p className='p'>{prCount} so'm</p>
                </div>
                </div>
                  <div className="right">
                  <button onClick={() => updateItemQuantity (item.id, item.count ++)}  className='btn btn-warning'>+</button>
                  <button className='btn btn-danger' onClick={() => updateItemQuantity (item.id, item.count --)}>-</button>
                  <img onClick={() => removeItem(item.id)} className='jjj' src="https://cdn-icons-png.flaticon.com/512/860/860778.png" alt=""   />
                    </div>                             
            </div>  
            </div>
          )
        } else {
          removeItem(item.id)
        }
       })}
  <aside>
  <div className="btnd">
    <img className='h11' src="https://logobank.uz:8005/media/logos_preview/preview_MW-01.png" alt="" />
  <div className="oplata">
    <h1 className='hh1'>Товары</h1>
  <h1 className='obshi'>{total}som</h1>
  </div>
  <div className="oplata">
    <h1 className='hh1'>Доставка</h1>
    <h1 className='obshi'>0 som</h1>
  </div>
  <button className='zakaz btn btn-danger' >Перейти к оформлению заказа</button>
  </div>
  </aside>
    </div>
  )
}

export default KorzinkaText