import React from 'react'
import "./Btns.scss"

import Slider from "react-slick";
import { HashLink } from 'react-router-hash-link';
// slider import>>>
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

const Btns = () => {

  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 4,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2
        }
      }
    ]
  };

  return (
    <div>
        <header>
            <div className="container">
                <div className="slider">
                    <Slider {...settings}>
                        <div>
                        <HashLink smooth to="#Max" className="btn1">🥤МаксиБокс</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Sendvich" className="btn1">🥪Клаб-Сэндвич</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Lavash" className="btn1">🌯Лаваш</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Shaurma" className="btn1">🌮Шаурма</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Donar" className="btn1">🍱Донар Кебаб</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Bagget"  className="btn1">🫔Шаурма Баггет</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Burger" className="btn1">🍔Бургеры</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Hotdog" className="btn1">🌭Хот-Дог</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Snek" className="btn1">🍟Снеки</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Napitka" className="btn1">🥤Напитки</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Desert" className="btn1">🍰Десерты</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Sous" className="btn1">🥫Соусы</HashLink>
                        </div>
                        <div>
                        <HashLink smooth to="#Garnir" className="btn1">🍚Гарниры</HashLink>
                        </div>                    
                        </Slider>
                </div>
            </div>
        </header>
    </div>
  )
}

export default Btns